body {
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
  overflow-y: auto;
}
body.bg-ua::before {
  content: "";
  position: fixed;
  inset: 0;
  pointer-events: none;
  background-image: radial-gradient(1.5px 1.5px at 15% 25%, rgba(255,140,50,0.4), transparent 50%),
    radial-gradient(1px 1px at 85% 15%, rgba(255,140,50,0.3), transparent 50%),
    radial-gradient(1.2px 1.2px at 45% 75%, rgba(255,140,50,0.35), transparent 50%),
    radial-gradient(0.8px 0.8px at 75% 85%, rgba(255,140,50,0.25), transparent 50%),
    radial-gradient(1px 1px at 25% 50%, rgba(255,140,50,0.3), transparent 50%),
    radial-gradient(1.3px 1.3px at 65% 35%, rgba(255,140,50,0.4), transparent 50%),
    radial-gradient(0.9px 0.9px at 35% 90%, rgba(255,140,50,0.2), transparent 50%),
    radial-gradient(1.1px 1.1px at 90% 60%, rgba(255,140,50,0.35), transparent 50%);
  opacity: 0.6;
  z-index: 0;
}
.geometric-shapes {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}
.geometric-shape {
  position: absolute;
  width: 80px;
  height: 80px;
  background: rgba(20, 20, 25, 0.8);
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(255, 140, 50, 0.25),
    0 2px 8px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 140, 50, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  transform-style: preserve-3d;
  animation: float-3d 8s ease-in-out infinite;
}
.geometric-shape.large {
  width: 120px;
  height: 120px;
  border-radius: 24px;
}
.geometric-shape.small {
  width: 60px;
  height: 60px;
  border-radius: 12px;
}
.shape-icon {
  color: rgba(255, 140, 50, 0.9);
  font-size: 24px;
  font-weight: 600;
}
.large .shape-icon {
  font-size: 36px;
}
.small .shape-icon {
  font-size: 18px;
}
@keyframes float-3d {
  0%, 100% {
    transform: translateY(0px) rotateX(0deg) rotateY(0deg);
  }
  25% {
    transform: translateY(-15px) rotateX(5deg) rotateY(10deg);
  }
  50% {
    transform: translateY(-25px) rotateX(-3deg) rotateY(-5deg);
  }
  75% {
    transform: translateY(-10px) rotateX(2deg) rotateY(8deg);
  }
}
.scroll-shape {
  transition: transform 0.1s ease-out;
}
.glow-pane {
  background: rgba(15, 15, 20, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 140, 50, 0.15);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5),
    0 0 20px rgba(255, 140, 50, 0.1);
}
nav, header, main, footer {
  position: relative;
  z-index: 10;
}
.glow-orange {
  box-shadow: 0 0 0 1px rgba(255, 140, 50, 0.1) inset,
    0 10px 30px rgba(255, 140, 50, 0.1),
    0 0 20px rgba(255, 140, 50, 0.05);
}
.btn-orange {
  background-image: linear-gradient(135deg, #ff7a1a 0%, #ffb347 100%);
  color: white;
  box-shadow: 0 8px 24px rgba(255, 140, 50, 0.25);
}
.btn-orange:hover {
  filter: brightness(1.05);
  box-shadow: 0 10px 30px rgba(255, 140, 50, 0.35);
}
.neon-orange {
  color: #ff7a1a;
  text-shadow: 0 0 10px rgba(255, 140, 50, 0.3);
}
.card-stat {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 14px 18px;
  border-radius: 1rem;
  background: rgba(15, 15, 20, 0.9);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 140, 50, 0.15);
  box-shadow: 0 8px 24px rgba(255, 140, 50, 0.15);
}
.icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background: linear-gradient(135deg, #ff7a1a, #ffb347);
  color: white;
  font-size: 20px;
  box-shadow: 0 0 12px rgba(255, 140, 50, 0.3);
}
.stat-icon {
  color: #ff8c32;
}
.stat-value {
  color: #ffffff;
  font-weight: 800;
}
.stat-label {
  color: #9ca3af;
  font-size: 12px;
  line-height: 1.1;
}
.btn-glass {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 140, 50, 0.2);
  backdrop-filter: blur(10px);
  color: #e5e7eb;
  border-radius: 9999px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
}
.btn-glass:hover {
  border-color: rgba(255, 140, 50, 0.4);
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}
.stat-card {
  background: rgba(15, 15, 20, 0.9);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  border-radius: 0.75rem;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 140, 50, 0.15);
  transition: all 0.3s ease;
}
.stat-card:hover {
  background: rgba(20, 20, 25, 1);
  box-shadow: 0 6px 22px rgba(255, 140, 50, 0.2);
  transform: translateY(-2px);
}
.icon-orange {
  color: #ff7a1a;
}
.stat-number {
  font-weight: 700;
  font-size: 1rem;
  color: #ff7a1a;
}
.stat-label {
  font-size: 0.75rem;
  color: #9ca3af;
}
.phone-frame {
  position: relative;
  border-radius: 2.5rem;
  background: rgba(15, 15, 20, 0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 140, 50, 0.15);
  padding: 1rem;
  transition: all 0.4s ease;
}
.phone-frame:hover {
  transform: translateY(-6px) rotate(1deg);
}
.phone-img {
  border-radius: 2rem;
  border: 1px solid rgba(255, 140, 50, 0.1);
  box-shadow: 0 0 30px rgba(255, 140, 50, 0.1),
    0 0 50px rgba(255, 140, 50, 0.05);
}
@layer components {
  .nav-glass {
    backdrop-filter: blur(24px) saturate(1.25);
    color: rgba(229,231,235,0.95);
    background: linear-gradient(180deg, rgba(255,255,255,.08), rgba(255,255,255,.04) 40%, rgba(0,0,0,.4));
    border: 1px solid rgba(255,140,50,.15);
    box-shadow: 0 10px 26px rgba(0,0,0,.5),
      0 1px 0 rgba(255,255,255,.1) inset;
  }
  .nav-glass-border {
    position: relative;
    isolation: isolate;
    background: linear-gradient(135deg,
      rgba(255,255,255,.15),
      rgba(255,255,255,.05) 45%,
      rgba(255,140,50,.2) 100%);
    border-radius: 44px;
  }
  .nav-glass-border::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    padding: 1.25px;
    background: linear-gradient(135deg,
      rgba(255,140,50,.4),
      rgba(255,255,255,.15) 50%,
      rgba(255,140,50,.25));
    -webkit-mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
    mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    opacity: .3;
    pointer-events: none;
  }
  .nav-link {
    transition-property: color;
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    color: #e5e7eb;
  }
  .nav-link:hover {
    color: #fb923c;
  }
  .btn-orange {
    background: linear-gradient(135deg,#ff7a1a,#ffb347);
    color: white;
    box-shadow: 0 0 0 1px rgba(255,140,50,.15) inset,
      0 8px 22px rgba(255,140,50,.2),
      0 2px 6px rgba(0,0,0,.1);
    transition: transform .15s ease, filter .15s ease;
  }
  .btn-orange:hover {
    filter: brightness(1.04);
  }
  .btn-orange:active {
    transform: translateY(1px);
  }
}
